@import "../variables";
.space{
  display: flex;
  flex-direction: column;
  align-items: center;
  .titleContainer{
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 10px;
  }
  .items{
    display: grid;
    margin-top: 100px;
    max-width: 1400px;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    @media only screen and (max-width: 900px) {
      grid-template-columns: repeat(1,1fr);
      margin-top: 50px;
    }
    .item{
      background: #380955;
      border: 5px solid rgba(255, 255, 255, 0.1);
      box-sizing: border-box;
      border-radius: 30px;
      padding: 8px;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 0 20px;
      img{
        width: 100%;
        height: 319px;
        object-fit: cover;
        border-radius: 30px;
      }
      .title{
        font-family: $Montserrat;
        font-style: normal;
        font-weight: 600;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #FFFFFF;
        margin-top: 15px;
      }
      .des{
        font-family: $Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 15px;
        line-height: 19px;
        text-align: center;
        color: #FFFFFF;
        margin-top: 7px;
        margin-bottom: 30px;
      }
    }
  }
  .desTitle{
    margin-top: 50px;
    font-family: $MontserratBold;
    font-style: normal;
    font-weight: 600;
    font-size: 35px;
    line-height: 43px;
    text-align: center;
    color: #FFFFFF;
    @media only screen and (max-width: 900px) {
      font-size: 20px;
      line-height: 24px;
    }
  }
  .desText{
    font-family: $Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 30px;
    line-height: 136.9%;
    text-align: center;
    color: #FFFFFF;
    margin-top: 15px;
    max-width: 1405px;
    padding: 0 20px;
    text-align: justify;
    @media only screen and (max-width: 900px) {
      font-size: 17px;
      line-height: 135%;
      padding: 0 16px;
    }
  }
  .partners{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    width: 100%;

    .title{
      font-family: $MontserratBold;
      font-style: normal;
      font-weight: 800;
      font-size: 40px;
      line-height: 49px;
      text-align: center;
      color: #FFFFFF;
      @media only screen and (max-width: 900px) {
        font-family: $MontserratBold;
        font-style: normal;
        font-weight: 800;
        font-size: 19px;
        line-height: 23px;
        text-align: center;
        color: #FFFFFF;
      }
    }
    .partnersItems{
      display: flex;
      width: 100%;
      max-width: 1402px;
      justify-content: space-between;
      margin-top: 70px;
      padding: 0 20px;
      @media only screen and (max-width: 900px) {
        padding: 0 16px;
        display: grid;
        grid-template-columns: repeat(2,1fr);
        grid-gap: 20px;
      }
      img{
        @media only screen and (max-width: 900px) {
          width: 100%;
          object-fit: contain;
        }

      }
    }
  }
  .premiumPartner{
    display: flex;
    width: 100%;
    max-width: 1402px;
    justify-content: space-between;
    align-items: center;
    margin-top: 70px;
    padding: 0 20px;
    @media only screen and (max-width: 900px) {
      flex-direction: column;
      margin-top: 50px;
    }
    .title{
      font-family: $MontserratBold;
      font-style: normal;
      font-weight: 800;
      font-size: 52px;
      line-height: 63px;
      text-align: center;
      color: #FFFFFF;
      position: relative;
      top: 20px;
      @media only screen and (max-width: 900px) {
         font-size: 17px;
        line-height: 21px;
        position: static;
      }
    }
    img{
      @media only screen and (max-width: 900px) {
        width: calc(100% - 32px);
        margin-top: 28px;
      }
    }
  }
  .video{
    display: flex;
    width: 100%;
    justify-content: center;
    max-width: 1402px;
    margin-top: 70px;
    padding: 0 20px;
    @media only screen and (max-width: 900px) {
      width: calc(100vw - 32px);
      height: calc(100vw - 32px);
      border-radius: 10px;
      overflow: hidden;
    }
    iframe{
      width: 100%;
      @media only screen and (max-width: 900px) {
        height: 100%;
      }
    }
  }
}