@import "../variables";
.aboutUs{
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  .titleContainer{
    margin-top: 100px;
  }
  .items{
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    @media only screen and (max-width: 1400px) {
      padding: 0 20px;
    }
    .item{
      display: flex;
      width: 100%;
      max-width: 1400px;
      background: radial-gradient(50% 50% at 50% 50%, #380955 0%, #300445 100%);
      border: 5px solid rgba(255, 255, 255, 0.1);
      border-radius: 30px;
      padding: 39px 25px;
      justify-content: space-between;
      margin-top: 50px;
      align-items: center;
      @media only screen and (max-width: 900px) {
        flex-direction: column;
        padding: 10px;
      }
      img{
        width: 424px;
        height: 424px;
        object-fit: contain;
        object-position: center;
        @media only screen and (max-width: 900px) {
          width: 100%;
          height: auto;
        }
      }
      .des{
        display: flex;
        flex-direction: column;
        width: calc(100% - (424px + 43px));
        justify-content: center;
        @media only screen and (max-width: 900px) {
          width: 100%;
          padding: 0 20px;
        }
        .title{
          font-family: $MontserratBold;
          font-style: normal;
          font-weight: 800;
          font-size: 25px;
          line-height: 30px;
          text-align: left;
          color: #FFFFFF;
          width: 100%;
          @media only screen and (max-width: 900px) {
            font-size: 16px;
            line-height: 20px;
            margin-top: 25px;
            text-align: center;
          }
        }
        .text{
          font-family: $Montserrat;
          font-style: normal;
          font-weight: normal;
          font-size: 20px;
          line-height: 24px;
          color: #FFFFFF;
          margin-top: 24px;
          @media only screen and (max-width: 900px) {
            font-size: 15px;
            line-height: 18px;
          }
        }
      }
    }
  }
}
.row-reverse{
  flex-direction: row-reverse;
}