@import "../variables";
.roadMap{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
  .titleContainer{
    display: flex;
    width: 100%;
    max-width: 1400px;
    height: 218px;
    justify-content: center;
    align-items: center;
    background-image: url("../../../assets/images/dotbg2.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: left center;
  }
  .content{
    display: flex;
    width: 100%;
    max-width: 1400px;
    margin-top: 83px;
    .leftSide{
      display: flex;
      width: 50%;
      flex-direction: column;
      @media only screen and (max-width: 900px) {
        display: none;
      }
      .item{
        display: flex;
        width: 100%;
        align-items: flex-end;
        margin-bottom: 30px;
        .image{

        }
        .icon{
          margin: 0 20px;
        }
        &:nth-child(2n){
          flex-direction: row-reverse;
        }
      }
    }
    .rightSide{
      display: flex;
      width: 50%;
      flex-direction: column;
      @media only screen and (max-width: 900px) {
        width: 100%;
      }
      .item{
        display: flex;
        align-items: flex-start;
        height: 335px;
        @media only screen and (max-width: 900px) {
          height: unset;
        }
        &:last-child{
          .secondImage{
            display: none;
            @media only screen and (max-width: 900px) {
              display: flex;
            }
          }
        }
        .icons{
          display: flex;
          flex-direction: column;
          .firstImage{
            object-fit: cover;
            width: 201px;
            object-position: top;
            position: relative;
            top: -45px;
            @media only screen and (max-width: 900px) {
              width: 80px;
              top: -20px;
            }
          }
          .secondImage{
            height: 193px;
            object-fit: contain;
            position: relative;
            top: -95px;
            @media only screen and (max-width: 900px) {
              width: 80px;
              top: -48px;
              height: 125px;
            }
          }
        }
        .des{
          display: flex;
          align-items: center;
          @media only screen and (max-width: 900px) {
            align-items: flex-start;
          }
          .number{
            font-family: $MontserratBold;
            font-style: normal;
            font-weight: 800;
            font-size: 80px;
            line-height: 98px;
            text-align: center;
            color: #FFFFFF;
            @media only screen and (max-width: 900px) {
              font-size: 30px;
              line-height: 37px;
              display: flex;
              align-items: flex-start;
            }
          }
          .text{
            display: flex;
            padding-left: 45px;
            flex-direction: column;
            @media only screen and (max-width: 900px) {
              padding-left: 25px;
            }
            .title{
              font-family: $MontserratBold;
              font-style: normal;
              font-weight: 800;
              font-size: 20px;
              line-height: 24px;
              text-align: center;
              color: #FFFFFF;
              text-align: left;
            }
            .subtitle{
              font-family: $Montserrat;
              font-style: normal;
              font-weight: normal;
              font-size: 15px;
              line-height: 23px;
              color: #FFFFFF;
              margin-top: 10px;
            }
          }
        }
      }
    }
  }
}