@import "../variables";
.firstScreen{
    display: flex;
    width: 100%;
    align-items: center;
    height: 1020px;
    flex-direction: column;
    background-image: url("../../../assets/images/firstscreenbg.png");
    background-repeat: no-repeat;
    background-size: cover;
    @media only screen and (max-width: 750px) {
        height: 485px;
    }
    .bigLogo{
        margin-top: 165px;
        @media only screen and (max-width: 1400px) {
            width: 80%;
        }
        @media only screen and (max-width: 900px) {
            margin-top: 100px;
            width: 288px;
            height: 87px;
        }
        img{
            @media only screen and (max-width: 1400px) {
                width: 100%;
                object-fit: contain;
            }
            @media only screen and (max-width: 900px) {
               height: 100%;
            }
        }
    }
    .date{
        display: flex;
        width: 1022px;
        height: 90px;
        justify-content: center;
        align-items: center;
        background: rgba(0, 0, 0, 0.2);
        border: 1px solid #0A68D1;
        box-sizing: border-box;
        backdrop-filter: blur(31px);
        border-radius: 104px;
        font-family: $Montserrat;
        font-style: normal;
        font-weight: 600;
        font-size: 35px;
        line-height: 43px;
        text-align: center;
        color: #FFFFFF;
        text-shadow: 3px 3px 5px #000000;
        margin-top: 50px;
        display: none;
        @media only screen and (max-width: 1200px) {
            width: 700px;
            font-size: 22px;
        }
        @media only screen and (max-width: 750px) {
            font-family: $Montserrat;
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 18px;
            text-align: center;
            color: #FFFFFF;
            text-shadow: 3px 3px 5px #000000;
            width: 288px;
            height: 55px;
            padding: 10px 24px;
        }
    }
    .button{
        display: flex;
        width: 350px;
        height: 80px;
        justify-content: center;
        align-items: center;
        background: linear-gradient(90deg, #4D0BA1 0%, #0470D5 100%);
        border-radius: 60px;
        font-family: $Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 24px;
        color: #FFFFFF;
        margin-top: 50px;
        position: relative;
        right: 35px;
        @media only screen and (max-width: 750px) {
           width: 288px;
            height: 55px;
            font-style: normal;
            font-weight: normal;
            font-size: 12px;
            line-height: 15px;
            color: #FFFFFF;
            position: static;
        }
    }

}