.futures{
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  .titleContainer{
    margin-top: 118px;
  }
  .subTitle{
    font-family: $Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 30px;
    line-height: 37px;
    text-align: center;
    color: #FFFFFF;
    margin-top: 20px;
    padding: 0 20px;
    @media only screen and (max-width: 900px) {
      font-size: 15px;
      line-height: 18px;
    }
    span{
    }
  }
  .items{
    display: grid;
    margin-top: 100px;
    max-width: 1400px;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    @media only screen and (max-width: 900px) {
      grid-template-columns: repeat(1,1fr);
    }
    .item{
      background: #380955;
      border: 5px solid rgba(255, 255, 255, 0.1);
      box-sizing: border-box;
      border-radius: 30px;
      padding: 8px;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 0 20px;
      img{
        width: 100%;
        height: 319px;
        object-fit: cover;
        border-radius: 30px;
      }
      .title{
        font-family: $Montserrat;
        font-style: normal;
        font-weight: 600;
        font-size: 30px;
        line-height: 37px;
        text-align: center;
        color: #FFFFFF;
        margin-top: 15px;
      }
      .des{
        font-family: $Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 15px;
        line-height: 19px;
        text-align: center;
        color: #FFFFFF;
        margin-top: 7px;
        margin-bottom: 30px;
      }
    }
  }
}