@import "../variables";
.about{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .titleContainer{
    display: flex;
    width: 100%;
    max-width: 1400px;
    align-items: center;
    justify-content: center;
    background-image: url("../../../assets/images/dotsbg.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    height: 263px;
  }
  .items{
    display: grid;
    width: 100%;
    max-width: 1400px;
    grid-template-columns: repeat(4,1fr);
    grid-gap: 40px;
    @media only screen and (max-width: 900px) {
      grid-template-columns: repeat(1,1fr);
    }
    .item{
      display: flex;
      flex-direction: column;
      margin-bottom: 80px;
      .title{
        font-family: $MontserratBold;
        font-style: normal;
        font-weight: 800;
        font-size: 30px;
        line-height: 37px;
        text-align: center;
        color: #FFFFFF;
      }
      .text{
        font-family: $Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 15px;
        line-height: 152.4%;
        text-align: center;
        color: #FFFFFF;
        margin-top: 20px;
      }
    }
  }
  .aboutSectionTitle{
    font-family: $MontserratBold;
    font-style: normal;
    font-weight: 800;
    font-size: 30px;
    line-height: 37px;
    text-align: center;
    color: #FFFFFF;
    margin-top: 20px;
  }
  .sectionText{
    font-family: $Montserrat;
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 202.4%;
    color: #FFFFFF;
    margin-top: 50px;
    max-width: 1430px;
    text-align: justify;
    padding: 20px 30px;
  }
}