@import "../variables";
.sectionTitle{
  height: 111px;
  @media only screen and (max-width: 900px) {
    height: unset;
  }
  .title1{
    font-family: $MontserratBold;
    font-style: normal;
    font-weight: 800;
    font-size: 80px;
    line-height: 98px;
    text-align: center;
    color: #FFFFFF;
    opacity: 0.05;
    text-transform: uppercase;
    @media only screen and (max-width: 900px) {
      font-size: 27px;
      line-height: 33px;
    }
  }
  .title2{
    font-family: $MontserratBold;
    font-style: normal;
    font-weight: 800;
    font-size: 45px;
    line-height: 55px;
    text-align: center;
    color: #FFFFFF;
    position: relative;
    bottom: 37px;
    text-transform: capitalize;
    @media only screen and (max-width: 900px) {
      font-size: 19px;
      line-height: 23px;
      bottom: 14px;
    }
  }
}