@import "../variables";
.footer{
  display: flex;
  width: 100%;
  justify-content: center;
  height: 60px;
  align-items: center;
  background: rgba(0, 0, 0, 0.43);
  backdrop-filter: blur(20px);
  .mobileMenu{
    display: flex;
    width: 100vw;
    position: fixed;
    top: 0;
    right: -100%;
    background: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(10px);
    flex-direction: column;
    z-index: 9999;
    transition: 0.7s;
    .nav{
      display: flex;
      flex-direction: column;
      margin-top: 60px;
      a{
        font-family: $MontserratBold;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
        display: flex;
        justify-content: center;
        margin-bottom: 30px;
      }
    }
    .socials{
      display: flex;
      width: 100%;
      justify-content: center;
      margin-bottom: 40px;
      a{
        margin: 0 12px;
      }
    }
    .closeButton{
      position: absolute;
      color: white;
      top: 20px;
      right: 20px;
      font-family: $Montserrat;
      font-size: 25px;
    }
  }
  .openMenu{
    right: 0;
  }
  @media only screen and (max-width: 1500px) {
    padding: 20px;
    height: unset;
  }
  @media only screen and (max-width: 900px) {
    padding: 16px;
    height: unset;
  }
  .container{
    display: flex;
    width: 100%;
    height: 100%;
    max-width: 1405px;
    justify-content: space-between;
    align-items: center;
    @media only screen and (max-width: 1500px) {
      flex-wrap: wrap;
    }
    nav{
      display: flex;
      align-items: center;
      @media only screen and (max-width: 900px) {
        display: none;
      }
      a{
        font-family: $Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
        margin: 0 12px;
        white-space: nowrap;
      }
    }
    .socials{
      display: flex;
      align-items: center;
      @media only screen and (max-width: 900px) {
        display: none;
      }
      a{
        margin: 0 11px;
      }
    }
    .connectButton{
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      width: 137px;
      height: 45px;
      background: linear-gradient(90deg, #A10B9E 0%, #D54F04 100%);
      border-radius: 60px;
      font-family: $Montserrat;
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 18px;
      color: #FFFFFF;
      border: none;
      outline: none;
      @media only screen and (max-width: 900px) {
        position: absolute;
        right: 60px;
      }
    }
    .mobileOpenButton{
      display: none;
      background: transparent;
      justify-content: center;
      align-items: center;
      width: 18px;
      height: 12px;
      border: none;
      outline: none;
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
      @media only screen and (max-width: 900px) {
        display: flex;
      }

    }
  }
}