@import "../variables";
.faq{
  display: flex;
  flex-direction: column;
  align-items: center;
  display: none;
  @media only screen and (max-width: 900px){
    display: none;
  }
  .titleContainer{
    display: flex;
    width: 100%;
    max-width: 1400px;
    align-items: flex-end;
    justify-content: center;
    background-image: url("../../../assets/images/faqbg.png");
    background-repeat: no-repeat;
    background-size: 100%;
    height: 263px;
  }
  .content{
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1200px;
    margin-top: 60px;
    .css-1fjvggn-MuiPaper-root-MuiAccordion-root{
      background: transparent;
      color: #FFFFFF;

    }
    .css-6cluhv-MuiButtonBase-root-MuiAccordionSummary-root{
      border-top: 1px solid white;
      border-bottom: 1px solid white;
    }
    .faqAccordionTitle{
      font-family: $MontserratBold;
      font-style: normal;
      font-weight: 600;
      font-size: 19px;
      line-height: 152.4%;
      color: #FFFFFF;
      padding: 20px 0;
      display: flex;
      align-items: center;
      img{
        margin-right: 60px;
      }
    }
    .faqAccordionText{
      font-family: $Montserrat;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 152.4%;
      color: #FFFFFF;
    }
  }
}