.ladyApes{
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  .titleContainer{
    margin-top: 100px;
  }
  .items{
    display: flex;
    justify-content: center;
    align-items: center;
    .item{
      display: flex;
      position: relative;
      filter: drop-shadow(0px 0px 30px rgba(0, 0, 0, 0.2));
      border-radius: 20px;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .big{
      width: 500px;
      height: 500px;
      z-index: 3;
      @media only screen and (max-width: 1600px){
        width: 300px;
        height: 300px;
      }
    }
    .medium{
      width: 400px;
      height: 400px;
      z-index: 2;
      @media only screen and (max-width: 1600px){
        width: 250px;
        height: 250px;
      }
    }
    .small{
      width: 300px;
      height: 300px;
      z-index: 1;
      @media only screen and (max-width: 1600px){
        width: 200px;
        height: 200px;
      }
    }
    .leftHand{
      right: -100px;
      @media only screen and (max-width: 1600px){
        right: -65px;
      }
    }
    .rightHand{
      left: -100px;
      @media only screen and (max-width: 1600px){
        left: -65px;
      }
    }
    .small.leftHand{
      right: -200px;
      @media only screen and (max-width: 1600px){
        right: -120px;
      }
    }
    .small.rightHand{
      left: -200px;
      @media only screen and (max-width: 1600px){
        left: -120px;
      }
    }
  }
}