@import "../variables";
.team{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 114px;
  .titleContainer{
    margin-top: 100px;
    display: flex;
    justify-content: center;
  }
  .items{
    display: grid;
    width: 100%;
    max-width: 1400px;
    grid-template-columns: repeat(4,1fr);
    grid-gap: 40px;
    justify-content: center;
    margin-top: 50px;
    padding: 0 20px;
    //@media only screen and (max-width: 850px){
    //  grid-template-columns: repeat(2,1fr);
    //  padding: 20px;
    //  overflow: hidden;
    //}

    @media only screen and (max-width: 1250px){
      grid-template-columns: repeat(3,1fr);
      padding: 20px;
      overflow: hidden;
    }
    @media only screen and (max-width: 900px){
      grid-template-columns: repeat(2,1fr);
      padding: 20px;
      overflow: hidden;
    }
    @media only screen and (max-width: 480px){
      grid-template-columns: repeat(1,1fr);
      padding: 20px;
      overflow: hidden;
    }

    .item{
      display: flex;
      flex-direction: column;
      position: relative;
      padding: 10px;
      background: linear-gradient(145.12deg, #BB9200 26.13%, #DE0093 47.27%, #00DEEC 69.37%, #1100D7 90.04%, #FFC700 109.74%);
      height: 510px;
      border-radius: 20px;
      @media only screen and (max-width: 900px){
        grid-template-columns: repeat(1,1fr);
      }
      .itemContent{
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 1px;
        left: 1px;
        width: calc(100% - 2px);
        height: calc(100% - 2px);
        background-image: radial-gradient(56.97% 111.45% at 50% 49.26%, #3D105B 0%, #240A3F 39%, #13062D 75%, #0D0526 100%);
        border-radius: 20px;
        align-items: center;
        img{
          width: calc(100%);
          height: 326px;
          object-fit: cover;
          object-position: top center;
          position: relative;
          top: 0px;
          border-radius: 20px;

        }
        .title{
          font-family: "MontserratBold", sans-serif;
          font-style: normal;
          font-weight: 600;
          font-size: 25px;
          line-height: 152.4%;
          color: #FFFFFF;
          text-align: center;
          position: relative;
          margin-top: 20px;
        }
        .text{
          font-family: $Montserrat;
          font-style: normal;
          font-weight: normal;
          font-size: 16px;
          line-height: 152.4%;
          text-align: center;
          color: #FFFFFF;
          padding: 0 3px;
          text-align: center;
          position: relative;
        }
      }
    }
  }
}